








































































































































































































import {Component, Vue, Prop} from 'vue-property-decorator'
import {Weather} from "@/store/types";

@Component
export default class Home extends Vue {
  @Prop() readonly weather!: Weather
  private time: Date = new Date(this.weather.dt * 1000)

  public refresh_weather(): void {
    this.$store.dispatch('weather/ADD_WEATHER', this.weather.id)
  }

  public delete_weather(): void {
    this.$store.commit('weather/REMOVE_WEATHER', this.weather.id)
    this.$store.dispatch('city/REMOVE_CITY', this.weather.id)
  }
}
